/** @jsx jsx */
import { jsx, Styled } from "theme-ui";
import { Component } from "react";
import { graphql } from "gatsby";
import { Layout } from "../components/common";
import { FaqHero, ControlPanel, QuestionsList } from "../components/faq";
import "aos/dist/aos.css";
import AOS from "aos";
import { SEO } from "theme-seo";

class FrequentlyAskedQuestions extends Component {
  state = {
    visibleQuestions: [],
    activeCategory: null,
    searchQuery: "",
    searchActive: false,
  };

  componentWillMount() {
    this.setState({ visibleQuestions: this.getAllQuestions() });
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    AOS.refresh();
  }

  getAllQuestions = () => {
    var qs = this.props.data.allFaqs.nodes.sort(
      (a, b) => b.frontmatter.order - a.frontmatter.order
    );
    const allQuestions = qs.map((q) => {
      const faq = {
        question: {
          text: q.frontmatter.question,
        },
        answer: {
          text: q.html,
          html: q.html,
        },
        category: q.frontmatter.category,
      };
      return faq;
    });
    return allQuestions;
  };

  setSearchHandler = (event) => {
    const { statusFilter } = this.state;
    const searchQuery = event.target.value.toLowerCase() || "";
    this.setState({ searchQuery });
    this.queryQuestions(statusFilter, searchQuery);
  };

  setCategoryHandler = (event, category) => {
    const { activeCategory, searchQuery } = this.state;
    if (event) event.preventDefault();
    if (category !== activeCategory) {
      this.setState({ activeCategory: category });
      this.queryQuestions(category, searchQuery);
    }
  };

  queryQuestions = (activeCategory, searchQuery) => {
    const questions = this.getAllQuestions();
    let visibleQuestions = questions;
    if (searchQuery) {
      visibleQuestions = questions.filter((faqItem) =>
        faqItem.question.text.toLowerCase().includes(searchQuery)
      );
    }
    if (activeCategory) {
      visibleQuestions = visibleQuestions.filter((faqItem) => {
        const thisCategory = faqItem.category;
        return activeCategory === thisCategory;
      });
    }
    this.setState({ visibleQuestions });
  };

  searchFocusHandler = (event, clicked) => {
    event.preventDefault();
    this.setState({ searchActive: clicked });
  };

  render() {
    const { searchActive, visibleQuestions, activeCategory, searchQuery } = this.state;
    const { location } = this.props;
    const questions = this.getAllQuestions();
    const seoData = {};
    const bannerTitle = "سوالات متداول";
    const bannerSubtitle = "You have questions, we have answers";
    const categories = questions.map((faqItem) => faqItem.category);
    const uniqueCategories = [...new Set(categories)];
    const faqSchemaData =
      questions &&
      questions.length > 0 &&
      questions.map((questionItem) => {
        const { question, answer } = questionItem;
        return {
          "@type": "Question",
          name: question.text,
          acceptedAnswer: {
            "@type": "Answer",
            text: answer.text,
          },
        };
      });
    return (
      <Styled.root>
        <Layout location={location} seoData={seoData}>
          <SEO
            title={this.props.pageContext.title}
            imageUrl={this.props.data.allFile.nodes?.[0].publicURL}
          />
          <FaqHero
            title={bannerTitle}
            subtitle={bannerSubtitle}
            location={location}
            imageFluid={this.props.data.allFile.nodes?.[0].childImageSharp?.fluid}
            publicURL={this.props.data.allFile.nodes?.[0].publicURL}
          />
          <ControlPanel
            activeCategory={activeCategory}
            uniqueCategories={uniqueCategories}
            setActiveCategoryHandler={this.setCategoryHandler}
            searchActive={searchActive}
            searchQuery={searchQuery}
            searchFocusHandler={this.searchFocusHandler}
            setSearchHandler={this.setSearchHandler}
            numResults={visibleQuestions.length}
          />
          <QuestionsList questions={visibleQuestions} location={location} />
        </Layout>
      </Styled.root>
    );
  }
}

export default FrequentlyAskedQuestions;

export const pageQuery = graphql`
  query {
    allFile(filter: { sourceInstanceName: { eq: "faq" }, name: { eq: "faq" } }) {
      nodes {
        name
        publicURL
        childImageSharp {
          fluid(maxWidth: 2500) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }

    allFaqs: allMarkdownRemark(
      filter: { fileAbsolutePath: { regex: "/faq/" } }
      sort: { fields: [frontmatter___date], order: ASC }
    ) {
      nodes {
        html
        frontmatter {
          question
          category
          order
        }
      }
    }
  }
`;
